.dropzone{position:relative;min-height:34px}.dropzone-overlay{position:absolute;top:0;left:0;width:100%;height:100%;opacity:0;z-index:-1;text-align:center;border-radius:0.25rem;background:rgba(250,226,72,0.65);-webkit-transition:opacity 0.1s, background 0.1s, z-index 0s 0.1s;transition:opacity 0.1s, background 0.1s, z-index 0s 0.1s}.ondrag .dropzone-overlay{opacity:1;z-index:1;-webkit-transition:opacity 0.1s, background 0.1s, z-index 0s 0s;transition:opacity 0.1s, background 0.1s, z-index 0s 0s}.dropzone-highlighted{background-color:rgba(0,123,255,0.5)}.dropzone-hint{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.image-uploader+.related-links{display:none}.image-uploader .form-control{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;-webkit-transition:background-color 0.2s 0s;transition:background-color 0.2s 0s}.image-uploader .file-info{font-size:.875em;font-weight:300}.image-uploader .progress{position:absolute;display:-webkit-box;display:-ms-flexbox;display:flex;left:0;bottom:0;width:100%;height:100%;border-radius:0;opacity:0;-webkit-transition:opacity 0.2s 0s;transition:opacity 0.2s 0s}.image-uploader.empty .form-control{border-top-right-radius:4px;border-top-right-radius:0.25rem;border-bottom-right-radius:4px;border-bottom-right-radius:0.25rem}.image-uploader.loading .form-control{background-color:#f5f5f5;border-top-right-radius:0;border-bottom-right-radius:0;-webkit-transition:background-color 0s;transition:background-color 0s}.image-uploader.loading .file-name,.image-uploader.loading .file-info{display:none}.image-uploader.loading .progress{opacity:0.5;-webkit-transition:opacity 0s;transition:opacity 0s}.image-uploader.loading .file-uploader__upload-button input{display:none}.image-uploader.processing .form-control{border-top-right-radius:4px;border-top-right-radius:0.25rem;border-bottom-right-radius:4px;border-bottom-right-radius:0.25rem}.image-uploader__ready-buttons,.image-uploader__loading-buttons{display:-webkit-box;display:-ms-flexbox;display:flex}.image-uploader__ready-buttons .input-group-text,.image-uploader__loading-buttons .input-group-text{border-top-left-radius:0;border-bottom-left-radius:0}.image-uploader__ready-buttons .input-group-text:not(:last-child),.image-uploader__loading-buttons .input-group-text:not(:last-child){border-top-right-radius:0;border-bottom-right-radius:0}.image-uploader.empty:not(.loading) .input-group-append,.image-uploader.empty.processing .input-group-append{display:none}.image-uploader:not(.loading) .image-uploader__loading-buttons,.image-uploader.processing .image-uploader__loading-buttons{display:none}.image-uploader.loading .image-uploader__ready-buttons{display:none}
